import { Component, ViewChild, viewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { STColumn, STColumnBadge, STColumnTag, STComponent, STData, STModule } from '@delon/abc/st';
import { NzButtonModule } from 'ng-zorro-antd/button';

const BADGE: STColumnBadge = {
  1: { text: '成功', color: 'success' },
  2: { text: '错误', color: 'error', tooltip: 'TIPS' },
  3: { text: '进行中', color: 'processing' },
  4: { text: '默认', color: 'default' },
  5: { text: '警告', color: 'warning' }
};
const TAG: STColumnTag = {
  1: { text: '成功', color: 'green' },
  2: { text: '错误', color: 'red' },
  3: { text: '进行中', color: 'blue' },
  4: { text: '默认', color: '' },
  5: { text: '警告', color: 'orange', tooltip: 'TIPS' }
};
const r = (min: number, max: number): number => Math.floor(Math.random() * (max - min + 1) + min);

@Component({
  selector: 'components-st-type',
  template: `
    <st
      #st
      [data]="users"
      [scroll]="{ x: '2000px', y: '2000px' }"
      [columns]="columns"
      [page]="{ position: 'bottom' }"
      [ps]="5"
      bordered
      size="middle"
    ></st>
  `,
  standalone: true,
  imports: [CommonModule, STModule, NzButtonModule]
})
export class ComponentsStTypeComponent {
  @ViewChild('st') public st!: STComponent;
  loading: boolean = true;
  users: STData[] = [];
  columns: STColumn[] = [
    {
      title: 'ID',
      index: 'id',
      width: 100
    },
    {
      title: 'Client Name',
      index: 'clientName',
      width: 150,
      fixed: 'left'
    },
    {
      title: 'B/L Number',
      index: 'blNumber',
      type: 'number',
      width: 100
    },
    {
      title: 'HS Code',
      index: 'hsCode',
      width: 100
    },
    {
      title: 'B/L Weight',
      index: 'blWeight',
      width: 100
    },
    {
      title: 'Manufacturer Name',
      index: 'manufacturerName',
      width: 200
    },
    {
      title: 'Notify Party Name',
      index: 'notifyPartyName',
      width: 200
    },
    {
      title: 'Cargo Upload Date',
      index: 'cargoxUploadDate',
      width: 200
    },
    {
      title: 'Containers Number',
      index: 'containersNumber',
      width: 100
    },
    {
      title: 'Invoice Date',
      index: 'invoiceDate',
      width: 200
    },
    {
      title: 'Invoice Number',
      index: 'invoiceNumber',
      width: 200
    },
    {
      title: 'Invoice Due Date',
      index: 'invoiceDueDate',
      width: 200
    },
    {
      title: 'Invoice Amount',
      index: 'invoiceAmount',
      width: 100
    },
    {
      title: 'Broker Name',
      index: 'brokerName',
      width: 200
    },
    {
      title: 'KG Price',
      index: 'kgPrice',
      width: 100
    },
    {
      title: 'Free Days',
      index: 'freeDays',
      width: 200
    },
    {
      title: 'POD',
      index: 'pod',
      width: 200
    },
    {
      title: 'POD Arrive Date',
      index: 'podArriveDate',
      width: 200
    },
    {
      title: 'Form 4 Status',
      index: 'form4Status',
      width: 200
    },
    {
      title: 'Storage Inv Fees',
      index: 'storageInvFees',
      width: 200
    },
    {
      title: 'Doc At Office Date',
      index: 'docAtOfficeDate',
      width: 200
    },
    {
      title: 'Material',
      index: 'material',
      width: 200
    },
    {
      title: 'Customs Finishing Date',
      index: 'customsFinishingDate',
      width: 200
    },
    {
      title: 'Customs Fees Receiving Date',
      index: 'customsFeesReceivingDate',
      width: 150
    },
    {
      title: 'ACID Number',
      index: 'acidNumber',
      width: 150
    },
    {
      title: 'Customs Certificate Number',
      index: 'customsCertificateNumber',
      width: 150
    },
    {
      title: 'Form 4 Bank',
      index: 'form4Bank',
      width: 150
    },
    {
      title: 'Due Days of Demurrage',
      index: 'dueDaysOfDemurrage',
      width: 150
    },
    {
      title: 'Total Amount Received',
      index: 'totalAmountReceived',
      width: 150
    }
  ];

  reload(): void {
    this.users = Array(100)
      .fill({})
      .map((_, i) => ({
        id: i + 1,
        clientName: `Clint ${i}`,
        blNumber: i + 1100,
        hsCode: `London ${i}`,
        blWeight: `${(i + 1) * 10} kg`,
        manufacturerName: `Manufacturer ${i}`,
        notifyPartyName: `Notify Party ${i}`,
        cargoxUploadDate: `2024-06-03`,
        containersNumber: i + 1,
        invoiceDate: `2024-06-03`,
        invoiceNumber: i + 2000,
        invoiceDueDate: `2024-06-10`,
        invoiceAmount: (i + 1) * 100,
        brokerName: `Broker ${i}`,
        kgPrice: (i + 1) * 5,
        freeDays: 10,
        pod: `POD ${i}`,
        podArriveDate: `2024-06-05`,
        form4Status: `Status ${i}`,
        storageInvFees: (i + 1) * 10,
        docAtOfficeDate: `2024-06-06`,
        material: `Material ${i}`,
        customsFinishingDate: `2024-06-07`,
        customsFeesReceivingDate: `2024-06-08`,
        acidNumber: `ACID ${i}`,
        customsCertificateNumber: `Certificate ${i}`,
        form4Bank: `Bank ${i}`,
        dueDaysOfDemurrage: 5,
        totalAmountReceived: (i + 1) * 200
      }));
  }

  constructor() {
    this.reload();
  }

}
