import { CommonModule } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { NzDividerModule } from 'ng-zorro-antd/divider';
import { NzTableModule } from 'ng-zorro-antd/table';
import { Observable, of } from 'rxjs';
interface DataItem {
  name: string;
  blNumber: number;
  hsCode: string;
  blWeight: string;
  manufacturerName: string;
  notifyPartyName: string;
  cargoxUploadDate: string;
  containersNumber: number;
  invoiceDate: string;
  invoiceNumber: number;
  invoiceDueDate: string;
  invoiceAmount: number;
  brokerName: string;
  kgPrice: number;
  freeDays: number;
  pod: string;
  podArriveDate: string;
  form4Status: string;
  storageInvFees: number;
  docAtOfficeDate: string;
  material: string;
  customsFinishingDate: string;
  customsFeesReceivingDate: string;
  acidNumber: string;
  customsCertificateNumber: string;
  form4Bank: string;
  dueDaysOfDemurrage: number;
  totalAmountReceived: number;
}

@Component({
  selector: 'gbsl-table-basic',
  template: `
    <nz-table
      nzShowPagination
      [nzLoading]="loading | async"
      nzShowSizeChanger
      #fixedTable
      [nzData]="listOfData"
      [nzScroll]="{ x: '2000px', y: '240px' }"
      (nzCurrentPageDataChange)="onCurrentPageDataChange($event)"
    >
      <thead>
        <tr>
          <th  nzLeft>Client Name</th>
          <th >B/L Number</th>
          <th >HS Code</th>
          <th >B/L Weight</th>
          <th >Manufacturer Name</th>
          <th >Notify Party Name</th>
          <th >Cargox Upload Date</th>
          <th >Containers Number</th>
          <th >Invoice Date</th>
          <th >Invoice Number</th>
          <th >Invoice Due Date</th>
          <th >Invoice Amount</th>
          <th >Broker Name</th>
          <th >KG Price</th>
          <th >Free Days</th>
          <th >POD</th>
          <th >POD Arrive Date</th>
          <th >Form 4 Status</th>
          <th >Storage Inv. Fees</th>
          <th >Doc At Office Date</th>
          <th >Material</th>
          <th >Customs Finishing Date</th>
          <th >Customs Fees Receiving Date</th>
          <th >Acid Number</th>
          <th >Customs Certificate Number</th>
          <th >Form 4 Bank</th>
          <th >Form 4 Bank</th>
          <th >Due Days Of Demurrage</th>
          <th >Total Amount Received</th>
        </tr>
      </thead>
      <tbody>
        <tr
          *ngFor="let data of listOfData; let i = index"
          [ngStyle]="{ 'background-color': i === 2 ? 'red' : 'white', color: i === 2 ? '' : 'black' }"
        >
          <td nzLeft>{{ data.name }}</td>
          <td>{{ data.blNumber }}</td>
          <td nzBreakWord>{{ data.hsCode }}</td>
          <td>{{ data.blWeight }}</td>
          <td>{{ data.manufacturerName }}</td>
          <td>{{ data.notifyPartyName }}</td>
          <td>{{ data.cargoxUploadDate }}</td>
          <td>{{ data.containersNumber }}</td>
          <td>{{ data.invoiceDate }}</td>
          <td>{{ data.invoiceNumber }}</td>
          <td>{{ data.invoiceDueDate }}</td>
          <td>{{ data.invoiceAmount }}</td>
          <td>{{ data.brokerName }}</td>
          <td>{{ data.kgPrice }}</td>
          <td>{{ data.freeDays }}</td>
          <td>{{ data.pod }}</td>
          <td>{{ data.podArriveDate }}</td>
          <td>{{ data.form4Status }}</td>
          <td>{{ data.storageInvFees }}</td>
          <td>{{ data.docAtOfficeDate }}</td>
          <td>{{ data.material }}</td>
          <td>{{ data.customsFinishingDate }}</td>
          <td>{{ data.customsFeesReceivingDate }}</td>
          <td>{{ data.acidNumber }}</td>
          <td>{{ data.customsCertificateNumber }}</td>
          <td>{{ data.form4Bank }}</td>
          <td>{{ data.dueDaysOfDemurrage }}</td>
          <td>{{ data.totalAmountReceived }}</td>
          <td>
            <a>action</a>
          </td>
        </tr>
      </tbody>
    </nz-table>
  `,
  standalone: true,
  imports: [NzTableModule, NzDividerModule, CommonModule],
  styles: [
    `
      th {
        background-color: #ededed !important;
      }
    `
  ]
})
export class NzDemoTableBasicComponent implements OnInit {
  listOfData: DataItem[] = [];
  loading!: Observable<boolean>;
  listOfCurrentPageData: readonly DataItem[] = [];

  ngOnInit(): void {
    for (let i = 0; i < 5; i++) {
      this.listOfData.push({
        name: `Edward King ${i}`,
        blNumber: i + 1,
        hsCode: `London ${i}`,
        blWeight: `${(i + 1) * 10} kg`,
        manufacturerName: `Manufacturer ${i}`,
        notifyPartyName: `Notify Party ${i}`,
        cargoxUploadDate: `2024-06-03`,
        containersNumber: i + 1,
        invoiceDate: `2024-06-03`,
        invoiceNumber: i + 1000,
        invoiceDueDate: `2024-06-10`,
        invoiceAmount: (i + 1) * 100,
        brokerName: `Broker ${i}`,
        kgPrice: (i + 1) * 5,
        freeDays: 10,
        pod: `POD ${i}`,
        podArriveDate: `2024-06-05`,
        form4Status: `Status ${i}`,
        storageInvFees: (i + 1) * 10,
        docAtOfficeDate: `2024-06-06`,
        material: `Material ${i}`,
        customsFinishingDate: `2024-06-07`,
        customsFeesReceivingDate: `2024-06-08`,
        acidNumber: `ACID ${i}`,
        customsCertificateNumber: `Certificate ${i}`,
        form4Bank: `Bank ${i}`,
        dueDaysOfDemurrage: 5,
        totalAmountReceived: (i + 1) * 200
      });
    }
    setTimeout(() => {
      this.loading = of(false);
    }, 5000);
  }
  onCurrentPageDataChange(listOfCurrentPageData: readonly DataItem[]): void {
    this.listOfCurrentPageData = listOfCurrentPageData;
  }
}
